	module.exports = {"data":{"challengeNode":{"title":"Use the justify-content Property in the Tweet Embed","description":["上一项挑战展示了<code>justify-content</code>属性的作用。如果我们想对齐推文内的子元素，可以把<code>justify-content</code>应用在<code>.profile-name</code>上。","<hr>","在 header 的<code>.profile-name</code>元素添加 CSS 属性<code>justify-content</code>，把它的值设为上面挑战提到的任意可用值。"],"challengeType":0,"fields":{"slug":"/responsive-web-design/css-flexbox/use-the-justify-content-property-in-the-tweet-embed","blockName":"CSS Flexbox","tests":[{"text":"<code>.profile-name</code>元素的<code>justify-content</code>属性可选以下值：center、flex-start、flex-end、space-between、space-around。","testString":"assert(code.match(/header\\s.profile-name\\s*{\\s*?.*?\\s*?.*?\\s*?\\s*?.*?\\s*?justify-content\\s*:\\s*(center|flex-start|flex-end|space-between|space-around)\\s*;/g), '<code>.profile-name</code>元素的<code>justify-content</code>属性可选以下值：center、flex-start、flex-end、space-between、space-around。');"}]},"required":[],"files":{"indexhtml":{"key":"indexhtml","ext":"html","name":"index","contents":"<style>\n  body {\n    font-family: Arial, sans-serif;\n  }\n  header, footer {\n    display: flex;\n    flex-direction: row;\n  }\n  header .profile-thumbnail {\n    width: 50px;\n    height: 50px;\n    border-radius: 4px;\n  }\n  header .profile-name {\n    display: flex;\n    flex-direction: column;\n    \n    margin-left: 10px;\n  }\n  header .follow-btn {\n    display: flex;\n    margin: 0 0 0 auto;\n  }\n  header .follow-btn button {\n    border: 0;\n    border-radius: 3px;\n    padding: 5px;\n  }\n  header h3, header h4 {\n    display: flex;\n    margin: 0;\n  }\n  #inner p {\n    margin-bottom: 10px;\n    font-size: 20px;\n  }\n  #inner hr {\n    margin: 20px 0;\n    border-style: solid;\n    opacity: 0.1;\n  }\n  footer .stats {\n    display: flex;\n    font-size: 15px;\n  }\n  footer .stats strong {\n    font-size: 18px;\n  }\n  footer .stats .likes {\n    margin-left: 10px;\n  }\n  footer .cta {\n    margin-left: auto;\n  }\n  footer .cta button {\n    border: 0;\n    background: transparent;\n  }\n</style>\n<header>\n  <img src=\"https://pbs.twimg.com/profile_images/378800000147359764/54dc9a5c34e912f34db8662d53d16a39_400x400.png\" alt=\"Quincy Larson's profile picture\" class=\"profile-thumbnail\">\n  <div class=\"profile-name\">\n    <h3>Quincy Larson</h3>\n    <h4>@ossia</h4>\n  </div>\n  <div class=\"follow-btn\">\n    <button>Follow</button>\n  </div>\n</header>\n<div id=\"inner\">\n  <p>I meet so many people who are in search of that one trick that will help them work smart. Even if you work smart, you still have to work hard.</p>\n  <span class=\"date\">1:32 PM - 12 Jan 2018</span>\n  <hr>\n</div>\n<footer>\n  <div class=\"stats\">\n    <div class=\"Retweets\">\n      <strong>107</strong> Retweets\n    </div>\n    <div class=\"likes\">\n      <strong>431</strong> Likes\n    </div>\n  </div>\n  <div class=\"cta\">\n    <button class=\"share-btn\">Share</button>\n    <button class=\"retweet-btn\">Retweet</button>\n    <button class=\"like-btn\">Like</button>\n  </div>\n</footer>","head":"","tail":""},"indexjs":null,"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/responsive-web-design/css-flexbox/align-elements-using-the-align-items-property","id":"587d78ac367417b2b2512af7"},"slug":"/responsive-web-design/css-flexbox/use-the-justify-content-property-in-the-tweet-embed"}}

